<template>
  <div>
    <!-- 양방향 데이터 바인딩 -->
    <input type="text" v-model="userId">
    <!-- v-model: value 속성값으로 들어감 -->
    <!-- value값은 문자열로 나옴 -->
    <!-- number로 받으려면 v-model.number 입력하면 숫자로 변환 가능 -->
    <button @click="myFunction" type="button">버튼</button>
    <button @click="changeData" type="button">변경</button>
    <br>
    <input type="text" v-model="num1"> +
    <input type="text" v-model="num2"> =
    <span>{{ num1 + num2 }}</span>
    <br>
    <input type="text" v-model.number="num3"> +
    <input type="text" v-model.number="num4"> =
    <span>{{ num3 + num4 }}</span>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      userId: 'testId01',
      num1: 0,
      num2: 0,
      num3: 0,
      num4: 0
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    // 함수는 methods 내부에 작성 function은 생략가능
    myFunction() {
      console.log(this.userId)
    },
    changeData() {
      this.userId = 'Jay'
    }
  }
}
</script>
