<template>
  <div>
    <!-- readonly에서는 양방향 데이터 바인딩하는 v-model 불필요. -->
    <!-- 속성에 데이터 바인딩 필요시 v-bind:속성 (v-bind 생략 가능) -->
    <input type="text" v-bind:value="userId" readonly>
    <input type="text" :value="userId" readonly>
    <br>
    <img :src="imgSrc" alt="" style="width: 100px; height: auto;">
    <br>
    <input type="search" name="" id="" v-model="txt1">
    <button type="button" :disabled="txt1 === ''">조회</button>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      userId: 'test01',
      imgSrc: 'https://logospng.org/wp-content/uploads/vue-js.png',
      txt1: ''
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {}
}
</script>
