<template>
  <div>{{ htmlstring }}</div>
  <!-- 태그는 v-html을 이용해야 함 -->
  <div v-html="htmlstring"></div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      htmlstring: '<p style="color:red;">빨간색 문자</p>'
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {}
}
</script>
