<template>
  <div>
    <!-- v-model은 value하고만 연결 된 것이 아님 체크박스는 checked 속성과 연결 -->
    <div>
      <input type="radio" id="html" value="HTML" v-model="favoriteLang">
      <label for="html">HTML</label>
    </div>
    <div>
      <input type="radio" id="css" value="CSS" v-model="favoriteLang">
      <label for="css">CSS</label>
    </div>
    <div>
      <input type="radio" id="js" value="JS" v-model="favoriteLang">
      <label for="js">JavaScript</label>
    </div>
  </div>
  <div>선택한 지역은 {{ favoriteLang }}</div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      // checkbox는 중복선택이 가능 때문에 배열로 받아야 함.
      favoriteLang: []
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {}
}
</script>
